@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:-webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.5)),to(rgba(0, 0, 0, 0.5))), url("https://images.unsplash.com/photo-1430609021175-3ff88910bb6d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80") no-repeat top center/cover;
  background:linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5)), url("https://images.unsplash.com/photo-1430609021175-3ff88910bb6d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80") no-repeat top center/cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-title{
    color: #F5F5F5;
    font-size: 80px;
    font-family: 'Josefin Sans';

}

.food-type{
    background-color: rgba(0, 0, 0, 0.4) !important;
    color: #F5F5F5;
    font-family: 'Playfair Display', serif;
}

.menu-wrapper{
    flex-direction: column;
}

.item-name{
    font-size: 20px;
}

.list-group-item{
    background-color: rgba(0, 0, 0, 0.5) !important;
    color: #F5F5F5;
    font-family: 'Playfair Display', serif;
}

.title-menu{
    color: #F5F5F5;
    font-family: 'Playfair Display', serif;
}

.cart-button{
    background-color: rgba(0, 0, 0, 0.5) !important;
    color: #F5F5F5;

}

.cart-font{
    color: #F5F5F5;
    font-family: 'Playfair Display', serif;
    font-size: 20px;
    background-color: rgba(0, 0, 0, 0.5);
}

.cart-items{
    background-color: rgba(0, 0, 0, 0.5) !important;
    color: #F5F5F5;
    font-family: 'Playfair Display', serif;
}

